import { useEffect, useState } from "react";
import {
  Container,
  Grid,
  Card,
  CardContent,
  Typography,
  Divider,
  Box,
  Stack,
} from "@mui/material";
import { bankPublic } from "../../../api/login/Login";

function CompanyBankDetails() {
  const [data, setData] = useState([]);

  const detailsBank = async () => {
    try {
      const res = await bankPublic();
      setData(res?.data?.data || []);
    } catch (error) {
      console.error("Error fetching bank details", error);
    }
  };

  useEffect(() => {
    detailsBank();
  }, []);

  return (
    <Container
      maxWidth="lg"
      sx={{
        minHeight: "100vh",
        backgroundColor: "var(--bs-primary3)",
        py: 5,
      }}
    >
      {/* Title */}
      <Typography
        sx={{
          fontSize: "28px",
          fontWeight: 700,
          color: "var(--bs-primary2)",
          textAlign: "center",
          mb: 5,
        }}
      >
        Company Bank Details
      </Typography>

      <Grid container spacing={4}>
        {data.map((item, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card
              sx={{
                height: "100%",
                borderRadius: "22px",
                background:"var(--bs-primary3)",
                border: "1px solid var(--bs-primary2)",
                boxShadow: "0 6px 20px rgba(0,0,0,0.06)",
                transition: "all 0.3s ease",
                "&:hover": {
                  transform: "translateY(-6px)",
                  boxShadow: "0 12px 28px rgba(0,0,0,0.08)",
                },
              }}
            >
              <CardContent sx={{ p: 3.2 }}>
                {/* Company Title */}
                <Box textAlign="center" mb={2}>
                  <Typography
                    sx={{
                      fontSize: "18px",
                      fontWeight: 700,
                      color: "var(--bs-primary2)",
                    }}
                  >
                    Unibcomp Networks Private Limited
                  </Typography>

                  <Divider
                    sx={{
                      mt: 1.5,
                      opacity: 0.6,
                    }}
                  />
                </Box>

                {/* Bank Details */}
                <Stack spacing={1.6}>
                  <InfoRow label="Bank Name" value={item.bank_name} />
                  <InfoRow
                    label="Account Number"
                    value={item.bank_account_number}
                  />
                  <InfoRow label="IFSC Code" value={item.ifsc_code} />
                  <InfoRow label="Bank Code" value={item.bank_code} />
                  <InfoRow label="Branch Name" value={item.branch_name} />
                </Stack>

                {/* Remarks */}
                {item.remark && (
                  <Box
                    sx={{
                      mt: 3,
                      p: 2,
                      borderRadius: "14px",
                      background: "#aeb9c715",
                      borderLeft: "4px solid #cc9840",
                    }}
                  >
                    <Typography
                      sx={{
                        fontSize: "13px",
                        fontWeight: 600,
                        color: "var(--bs-primary2)",
                        mb: 0.5,
                      }}
                    >
                      Remarks
                    </Typography>

                    <Typography
                      sx={{
                        fontSize: "13px",
                        color: "var(--bs-primary2)",
                        opacity: 0.8,
                      }}
                    >
                      {item.remark}
                    </Typography>
                  </Box>
                )}
              </CardContent>
            </Card>
          </Grid>
        ))}

        {/* Empty State */}
        {data.length === 0 && (
          <Grid item xs={12}>
            <Box
              sx={{
                textAlign: "center",
                py: 8,
                color: "var(--bs-primary2)",
                opacity: 0.7,
              }}
            >
              <Typography fontSize="18px" fontWeight={600}>
                No bank details available
              </Typography>
            </Box>
          </Grid>
        )}
      </Grid>
    </Container>
  );
}

/* Enhanced Info Row */
const InfoRow = ({ label, value }) => (
  <Box
    sx={{
      display: "flex",
      justifyContent: "space-between",
      gap: 2,
      alignItems: "center",
      py: 0.6,
    }}
  >
    <Typography
      sx={{
        fontSize: "14px",
        fontWeight: 600,
        color: "var(--bs-primary2)",
        opacity: 0.85,
      }}
    >
      {label}
    </Typography>

    <Typography
      sx={{
        fontSize: "14px",
        fontWeight: 500,
        color: "var(--bs-primary2)",
        textAlign: "right",
        wordBreak: "break-all",
      }}
    >
      {value || "—"}
    </Typography>
  </Box>
);

export default CompanyBankDetails;
